import {Injectable} from "@angular/core";
import {Subject} from "rxjs/index";

@Injectable()
export class SharedService{

  /* 菜单高亮广播 begin */
  private NavChangeSource = new Subject<any>();
  NavChangeEmitted$ = this.NavChangeSource.asObservable();

  emitNavChange(change: any) {
    this.NavChangeSource.next(change);
  }
  /* 菜单高亮广播 end*/
  
}

